<template>
    <div class="charts-page">
        <div class="top-card">
            <techo-card resizeEvent="topCard" @onUpLevel="handleUpLevel" :maxCard="true" :upLevel="isShowLevelBtu">
                <div slot="header">综治机构区域分布</div>
                <div id="TopBar" style="height:100%"></div>
            </techo-card>
        </div>
        <el-row class="bottom-card" :gutter="10">
            <el-col :span="12">
                <techo-card resizeEvent="leftCard" :maxCard="true">
                    <div slot="header">综治机构层级分布</div>
                    <div id="LeftBar" style="height:100%"></div>
                </techo-card>
            </el-col>
            <el-col :span="12">
                <techo-card resizeEvent="rightCard" :maxCard="true">
                    <div slot="header">综治机构类型占比</div>
                    <div id="RightPie" style="height:100%"></div>
                </techo-card>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import barOptions from "./config/barOptions.js";
import pieOptions from "./config/pieOptions.js";
import service from "./services/zz_service.js";
export default {
  data() {
    return {
      DISCODE: "6102",
      // 综治机构分布
      topBarOptions: {},
      topBarChart: null,
      // 综治机构各级别统计
      leftBarOptions: {},
      leftBarChart: null,
      // 综治机构类型占比
      rightPieOptions: {},
      rightPieChart: null,
      // 当前柱状图级别code
      byCode: "",
      isShowLevelBtu: true,
      COLORS: ["#3965c3", "#ffa639", "#f75a51"]
    };
  },
  watch: {
    byCode(value) {
      this.isShowLevelBtu = value.length == 6 ? false : true;
    }
  },
  mounted() {
    let that = this;
    // 综治机构区域分布
    this.topBarChart = this.$echarts.init(this.$el.querySelector("#TopBar"));
    // 综治机构各级别统计
    this.leftBarChart = this.$echarts.init(this.$el.querySelector("#LeftBar"));
    // 综治机构类型占比
    this.rightPieChart = this.$echarts.init(
      this.$el.querySelector("#RightPie")
    );
    this.topBarChart.on("click", val => {
      if (this.byCode.length != 14) {
        this.getOrgData(val.data.rawdata.code);
        this.getOrgPieData(val.data.rawdata.code);
      }
    });
    // 初始化统计图自适应函数
    this.$bus.$on("topCard", () => {
      that.topBarChart.resize();
    });
    this.$bus.$on("leftCard", () => {
      that.leftBarChart.resize();
    });
    this.$bus.$on("rightCard", () => {
      that.rightPieChart.resize();
    });
  },
  created() {
    /** 查询综治机构数据数据 */
    this.getOrgData();
    /** 查询各级别综治机构数据 */
    this.getLevelOrgData();
    /** 查询综治机构占比 */
    this.getOrgPieData();
  },

  methods: {
    // 各分区综治机构
    getOrgData(disCode) {
      let param = {
        districtCode: disCode || this.DISCODE,
        entityName: "ViewCompreOrganization",
        fieldName: "type"
      };
      let that = this;
      this.topBarOptions = _.cloneDeep(barOptions);
      service.requestByEntityAndField(param, data => {
        if (data.length == 0) return;
        this.byCode = data[0].code;
        this.topBarOptions.yAxis.name = "机构数";
        let legend = _.keys(_.groupBy(data, "keyName"));
        this.topBarOptions.legend.data = legend;
        _.map(legend, (value, index) => {
          that.topBarOptions.series.push({
            name: value,
            type: "bar",
            itemStyle: {
              normal: {
                color: that.COLORS[index]
              }
            },
            barMaxWidth: 80,
            data: []
          });
        });
        _.map(_.groupBy(data, "name"), (item, key) => {
          that.topBarOptions.xAxis.data.push(key);
          _.map(item, value => {
            let serie = _.find(that.topBarOptions.series, name => {
              return name.name == value.keyName;
            });
            serie.data.push({
              rawdata: value,
              value: value.count
            });
          });
        });
        this.topBarChart.setOption(that.topBarOptions);
        this.topBarChart.resize();
      });
    },
    // 各级别综治机构
    getLevelOrgData() {
      let param = {
        entityName: "ViewCompreOrganization",
        fieldName: "type"
      };
      // P地市 R区县 S乡镇 T社区 U网格
      let that = this;
      this.leftBarOptions = _.cloneDeep(barOptions);
      service.requestByLevelField(param, data => {
        this.leftBarOptions.yAxis.name = "机构数";
        let legend = _.keys(_.groupBy(data, "keyName"));
        this.leftBarOptions.legend.data = legend;
        _.map(legend, (value, index) => {
          that.leftBarOptions.series.push({
            name: value,
            type: "bar",
            itemStyle: {
              normal: {
                color: that.COLORS[index]
              }
            },
            data: []
          });
        });
        _.map(_.groupBy(_.sortBy(data, "name"), "name"), (item, key) => {
          let xItem = "";
          switch (key) {
            case "P":
              xItem = "市";
              break;
            case "R":
              xItem = "区县";
              break;
            case "S":
              xItem = "乡镇";
              break;
            case "T":
              xItem = "社区";
              break;
            case "U":
              xItem = "网格";
              break;
            default:
              break;
          }
          that.leftBarOptions.xAxis.data.push(xItem);
          _.map(item, value => {
            let serie = _.find(that.leftBarOptions.series, name => {
              return name.name == value.keyName;
            });
            serie.data.push(value.count);
          });
        });
        this.leftBarChart.setOption(that.leftBarOptions);
        this.leftBarChart.resize();
      });
    },
    // 综治机构占比
    getOrgPieData(disCode) {
      let param = {
        districtCode: disCode || this.DISCODE,
        entityName: "ViewCompreOrganization",
        fieldName: "type"
      };
      let that = this;
      this.rightPieOptions = _.cloneDeep(pieOptions);
      service.requestByFieldName(param, data => {
        that.rightPieOptions.legend.data = _.keys(_.groupBy(data, "name"));
        that.rightPieOptions.title.text = " ";
        that.rightPieOptions.series[0].name = "综治领导责任制";
        _.map(data, item => {
          that.rightPieOptions.series[0].data.push({
            name: item.name,
            value: item.count
          });
        });
        that.rightPieChart.setOption(that.rightPieOptions);
        that.rightPieChart.resize();
      });
    },
    /** 返回上一级 */
    handleUpLevel() {
      let districtCode = "";
      switch (this.byCode.length) {
        case 9:
          districtCode = this.byCode.slice(0, 4);
          break;
        case 12:
          districtCode = this.byCode.slice(0, 6);
          break;
        case 14:
          districtCode = this.byCode.slice(0, 9);
          break;
        default:
          break;
      }
      this.getOrgData(districtCode);
      this.getOrgPieData(districtCode);
    }
  }
};
</script>

<style lang="scss">
.charts-page {
  padding: 10px 5px;
  .top-card {
    height: 20rem;
  }
  .bottom-card {
    padding-top: 0.6rem;
    position: absolute;
    height: calc(100% - 21.2rem);
    width: 100%;
    .el-col-12 {
      height: 100%;
    }
  }
}
</style>
